.form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 650px;
    /* min-height: 640px; */
    background-image: url('../images/background1.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    overflow: hidden;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 300px;
  }
  
  input {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .login-btn {
    background-color: #4CAF50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .error {
    color: red;
    margin-bottom: 10px;
  }

  .credentials {
    border: 1px solid #185519;
    padding: 10px 20px;
    margin-top: 20px;
    border-radius: 10px;
    background-color: bisque;
  }

  .credentials .title {
    color: #185519;
  }

  .credentials .uname, .credentials .pname {
    color: #185519;
  }

  /* .credentials .uname:hover, .credentials .pname:hover {
    color: blue;
    cursor: pointer;
  }

  .credentials .label:hover, .credentials .label:hover {
    color: blue;
  } */

  .usernamebtn, .passwordbtn {
    font-size: 10px;
    background-color: #185519;
    color: white;
    padding: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  .copySuccess {
    font-size: 14px;
    color: red;
    margin-left: 10px;
  }

